:root {
  --bg-white: #fff;
  --bg-overlay: rgba(0, 0, 0, 0.3);
  --text-color: #000;
  --close-icon-color: #000;
}

.custom-modal-overlay {
  background-color: var(--bg-overlay);
  display: none;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: 999;
  align-items: center;
  justify-content: center;
}

.custom-modal {
  display: none;
  z-index: 1;
  width: 600px;
  height: 400px;
  overflow: auto;
  background-color: var(--bg-white);
  position: relative;
}

.custom-modal-content {
  background-color: var(--bg-white);
  padding: 20px;
  width: 100%;
}

.custom-modal-close {
  color: var(--close-icon-color);
  float: right;
  font-size: 24px;
  position: absolute;
  top: 15px;
  right: 20px;
}

.custom-modal-close:hover,
.custom-modal-close:focus {
  color: var(--text-color);
  text-decoration: none;
  cursor: pointer;
}
